<template>
    <div class="boxTotal">
        <div v-for="item,i in list" :style="'background-image:url('+item.url+')'" class="box" :key="i" @mouseover="overimg(item,$event)" @mouseleave="leaveimg(item,$event)">
            <div class="icontext">
            {{item.msg}}

            </div>
            </div>
    </div>
</template>

<script>
export default {
    name:"Box4",
    data(){
        let list=[
            {
                url:"http://180.167.126.199:8000/static/img/danweiunselected@2x.dad7aea.png",
                hoverurl:"http://180.167.126.199:8000/static/img/danweiselected@2x.37ee0f0.png",
                msg:"文明单位创建"
            },
            {
                url:"http://180.167.126.199:8000/static/img/danweiunselected@2x.dad7aea.png",
                hoverurl:"http://180.167.126.199:8000/static/img/danweiselected@2x.37ee0f0.png",

                msg:"文明单位创建"
            },
            {
                url:"http://180.167.126.199:8000/static/img/danweiunselected@2x.dad7aea.png",
                hoverurl:"http://180.167.126.199:8000/static/img/danweiselected@2x.37ee0f0.png",

                msg:"文明单位创建"
            },
            {
                url:"http://180.167.126.199:8000/static/img/danweiunselected@2x.dad7aea.png",
                hoverurl:"http://180.167.126.199:8000/static/img/danweiselected@2x.37ee0f0.png",

                msg:"文明单位创建"
            },
        ]
        return{list}
    },
    methods:{
        overimg(item,e){
            e.target.style.removeProperty("backgroundImage")
            e.target.style.backgroundImage="url("+item.hoverurl+")"
        },
        leaveimg(item,e){
            e.target.style.removeProperty("backgroundImage")
            e.target.style.backgroundImage="url("+item.url+")"

        }
    }
}
</script>

<style scoped>
.boxTotal{
    width:100%;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    margin: 20px auto 0 auto;
}
.box{
    margin: 20px;
    transition: all 0.7s ease-out;
    background-repeat: no-repeat;
    background-position: 50%;
    background-size: 40%;
    position: relative;
        cursor: pointer;
    border: 1px solid #dcdcdc;

}
.box:hover{
    background-color: #ED9609;
    color: white;
}
.icontext{
    width: 100%;
    font-size: 15px;
    font-weight: 600;
    position: absolute;
    left:50%;
    bottom: 10%;
    transform: translate(-50%,0);
    text-align: center;
    pointer-events: none;
    /* 溢出隐藏 */

    overflow: hidden;

  /* 限制一行显示 */

    white-space: nowrap;

  /* 显示不下省略号显示 */

    text-overflow: ellipsis;
}
@media(min-width:1200px){
    .box{
        width: 235px;
        height:288px;
    }
}
@media(min-width:1000px) and (max-width:1200px){
    .box{
        width: 445px;
        height:288px;
    }
}
@media(min-width:800px) and (max-width:1000px){
    .box{
        width: 345px;
        height:288px;
    }
}
@media(max-width:800px) {
    .box{
        margin: 10px;
        width: 40%;
        height:144px;

    }
    .icontext{
        font-size:10px;
    }
}
</style>